\n\t\tflex-direction: column;\n\t\tpadding-block: var(--default-grid-baseline) 0;\n\t\tpadding-inline: 0 var(--default-grid-baseline);\n\n\t\t> :deep(li) {\n\t\t\tbox-sizing: border-box;\n\t\t\t// basically \"fit-content\"\n\t\t\tflex: 0 1;\n\t\t}\n\t}\n}\n"],"sourceRoot":""}]);
